@import '../../styles/global.scss';

.App {
  height: 100vh;
  background: radial-gradient(140.67% 117.32% at 100% 98.5%, #00fff0 0%, #c0f 100%), #fff;

  .emoji-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .topBar {
    margin-top: 2vh * $scaler;
  }

  .buttons {
    display: flex;
    margin: 4vh * $scaler;
    border-radius: 2vh * $scaler;
    background: rgba(0, 0, 0, 0.4) !important;
  }

  .score {
    display: flex;
    color: white;
    text-transform: uppercase;
    font-size: 2vh * $scaler;
    font-weight: bold;
    border-radius: 1vh * $scaler;
    // margin: 2vh * $scaler;
    // padding: 1vh * $scaler 1.5vh * $scaler;
    background: rgba(0, 0, 0, 0.4) !important;
    box-shadow: 0 0 1vh * $scaler rgba(0, 0, 0, 0.62);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
  }

  .scale {
    &-enter {
      opacity: 0;
      transform: scale(0);

      &-active {
        opacity: 1;
        transform: scale(1) rotate(0deg);
        transition: all 300ms ease-in-out 100ms;
      }

      &-done {
        opacity: 1;
        transform: scale(1);
      }
    }

    &-exit {
      opacity: 1;
      transform: scale(1);

      &-active {
        opacity: 0 !important;
        transform: scale(0);
        transition: all 300ms ease-in-out;
      }

      &-done {
        opacity: 0;
        transform: scale(0);
      }
    }
  }

  .score-holder {
    position: relative;
    width: 6vh;
    height: 3vh;
    margin: 1.5vh * $scaler 0.65vh * $scaler;
    margin-bottom: 3vh * $scaler;
    padding: 0.75vh * $scaler 1vh * $scaler;

    &.timer {
      width: 3vh;
    }
  }

  .pair-holder {
    position: relative;
    width: 20vh;
    height: 20vh;

    // &.three {
    //   width: 23vh;
    //   height: 34vh;
    // }

    &:first-child {
      border: none;
      border-bottom: 0.2vh * $scaler dotted rgba(255, 255, 255, 0.425);
      margin-bottom: 2.5vh * $scaler;
      padding-bottom: 2.5vh * $scaler;
    }
  }

  .debug {
    margin: 0;
    padding: 0.5vh * $scaler;
  }

  .pairs {
    display: flex;
    flex-direction: column;
    border-radius: 2vh * $scaler;
    padding: 2vh * $scaler;
  }

  .gameField {
    display: flex;
    justify-content: center;
    user-select: none;
    flex-direction: column;
    align-items: center;
    max-height: 100vh;
    // background: radial-gradient(140.67% 117.32% at 100% 98.5%, #00fff0 0%, #c0f 100%), #fff;
  }

  .pair {
    width: 100%;
    display: flex;
    flex-direction: column;
    top: 0;
    left: 0;
    position: absolute;
  }

  .thumb {
    background: #c0f;
    -webkit-appearance: none;
    border: none;
    border-radius: 2vh * $scaler;
    cursor: pointer;
    user-select: none;
    transition: all linear 100ms;
    padding: 0.5vh * $scaler;
    box-shadow: 0 0.4vh * $scaler 2vh * $scaler #be11fe;
    touch-action: manipulation;

    &:active {
      transform: scale(0.8);
      opacity: 1;
    }

    &.up {
      margin-left: 2vh * $scaler;
    }

    &.switch {
      font-size: 3vh * $scaler;
      background: none;
      box-shadow: none;
      padding: 0;
    }

    &.down {
      margin-right: 2vh * $scaler;
      background: #00fff0;
      box-shadow: 0 0.4vh * $scaler 2vh * $scaler #1bdcf1;
    }
  }
}
